export const setThemeOnLoad = function () {
  if (
    localStorage.theme === 'dark' ||
    (!('theme' in localStorage) &&
      window.matchMedia('(prefers-color-scheme: dark)').matches)
  ) {
    state.theme = 'dark';
    // localStorage.setItem('theme', 'dark');
  } else {
    state.theme = 'light';
    // localStorage.setItem('theme', 'light');
  }
};
